<template>
  <div>
    <!--  -->
    <el-row :gutter="20" class="card-container">
      <el-col :span="6">
        <div class="data-card">
          <h3>{{ 'API' }}</h3>
          <p>{{ cards.users }}</p>
        </div>
      </el-col>
      <el-col :span="6">
        <div class="data-card">
          <h3>{{ '' }}</h3>
          <p>{{ cards.tasks }}</p>
        </div>
      </el-col>
      <el-col :span="6">
        <div class="data-card">
          <h3>{{ '' }}</h3>
          <p>{{ cards.regions }}</p>
        </div>
      </el-col>
      <el-col :span="6">
        <div class="data-card">
          <h3>{{ '' }}</h3>
          <p>{{ cards.days }}</p>
        </div>
      </el-col>
    </el-row>

    <!---->
    <div id="map" ref="mapContainer" v-loading="loading"></div>

    <!--  -->
    <el-row :gutter="20" class="chart-container">
      <el-col :span="12">
        <!--  -->
        <el-row :gutter="20">
          <el-col :span="12">
            <div class="chart" ref="cpuChart"></div>
          </el-col>
          <el-col :span="12">
            <div class="chart" ref="memoryChart"></div>
          </el-col>
        </el-row>
      </el-col>
      <el-col :span="12">
        <!--  -->
        <div class="chart" ref="trafficChart"></div>
      </el-col>
    </el-row>


  </div>
</template>

<script>
import * as echarts from 'echarts';
import L from 'leaflet';
import 'leaflet/dist/leaflet.css';
import 'leaflet/dist/leaflet.js';

export default {
  data() {
    return {
      loading: false,
      cards: {
        users: 0,
        tasks: 0,
        regions: 0,
        days: 0,
        cities: []
      },
      cpuChart: null,
      memoryChart: null,
      trafficChart: null,
      ws: null,
      reconnectAttempts: 0,
      maxReconnectAttempts: 5,
      reconnectDelay: 5000,
      manualClose: false,
      isReconnecting: false,
      map: null,
      markers: [],
      cities: [
        {lat: 40.7128, lng: -74.0060, name: ""},
        {lat: 51.5074, lng: -0.1278, name: ""},
        {lat: 35.6895, lng: 139.6917, name: ""},
        {lat: -33.8688, lng: 151.2093, name: ""},
        {lat: 48.8566, lng: 2.3522, name: ""},
        {lat: 55.7558, lng: 37.6173, name: ""},
        {lat: 19.4326, lng: -99.1332, name: ""},
        {lat: -23.5505, lng: -46.6333, name: ""},
        {lat: 1.3521, lng: 103.8198, name: ""},
        {lat: 30.0444, lng: 31.2357, name: ""},
      ],
    };
  },
  mounted() {
    this.initInfo();
    this.initCharts();
    this.initWebSocket();
    // 
    this.$router.beforeEach((to, from, next) => {
      this.closeWebSocket();
      next();
    });
  },
  beforeUnmount() {
    this.closeWebSocket();
  },
  methods: {
    async initMap() {
      this.loading = true
      delete L.Icon.Default.prototype._getIconUrl;

      L.Icon.Default.mergeOptions({
        iconRetinaUrl: require('leaflet/dist/images/marker-icon-2x.png'),
        iconUrl: require('leaflet/dist/images/marker-icon.png'),
        shadowUrl: require('leaflet/dist/images/marker-shadow.png')
      });
      this.map = L.map(this.$refs.mapContainer);

      L.tileLayer("https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png", {
        // attribution: ' <a href="https://openstreetmap.org">OpenStreetMap</a> contributors',
        maxZoom: 18,
      }).addTo(this.map);

      const bounds = [];
      if (this.cards.cities.length === 0) {
        this.loading = false
        return
      }
      for (const city of this.cards.cities) {
        let popupContent = `
          ${city.country}<br>
          ${city.area}<br>
          ${city.city}<br>
          ${city.org}<br>
          ASN${city.asn}<br>
          ${city.count}<br>
          ${city.lat}<br>
          ${city.lng}
        `;
        const marker = L.marker([city.lat, city.lng])
            .addTo(this.map)
            .bindPopup(popupContent);

        this.markers.push(marker);
        bounds.push([city.lat, city.lng]);
      }

      if (bounds.length === 1) {
        this.map.setView(bounds[0], 4);
      } else {
        this.map.fitBounds(bounds);
      }

      this.loading = false
    },
    initInfo() {
      this.$axios.get('/sys/glance')
          .then(response => {
            if (response.data.success) {
              this.cards = response.data.data
              localStorage.setItem('currentVersion', response.data.data.currentVersion);
              this.initMap();
            }
          })
          .catch(error => {
            console.error("Error fetching glance data:", error);
            throw error;
          })
    },
    initCharts() {
      this.cpuChart = echarts.init(this.$refs.cpuChart);
      this.memoryChart = echarts.init(this.$refs.memoryChart);
      this.trafficChart = echarts.init(this.$refs.trafficChart);

      this.cpuChart.setOption({
        title: {text: 'CPU %', left: 'center'},
        tooltip: {trigger: 'item'},
        series: [{
          name: 'CPU %',
          type: 'pie',
          radius: '50%',
          data: [],
        }],
      });

      this.memoryChart.setOption({
        title: {text: 'Memory %', left: 'center'},
        tooltip: {trigger: 'item'},
        series: [{
          name: 'Memory %',
          type: 'pie',
          radius: '50%',
          data: [],
        }],
      });

      this.trafficChart.setOption({
        title: {text: 'KB/s', left: 'center'},
        tooltip: {
          trigger: 'axis',
          formatter: function (params) {
            let result = params[0].axisValue + '<br/>';
            params.forEach(param => {
              // 
              result += `${param.seriesName}: ${param.value} KB/s<br/>`;
            });
            return result;
          }
        },
        legend: {  // 
          data: ['', ''],
          bottom: 15
        },
        xAxis: {
          type: 'category',
          data: [],
          axisLabel: {
            rotate: 30  // 
          }
        },
        yAxis: {
          type: 'value',
          axisLabel: {
            formatter: '{value}'
          }
        },
        series: [
          {
            name: '',
            type: 'line',
            data: [],
            smooth: true,  // 
            showSymbol: true  // 
          },
          {
            name: '',
            type: 'line',
            data: [],
            smooth: true,
            showSymbol: true
          }
        ]
      });
    },

    async closeWebSocket() {
      if (this.ws) {
        if (this.ws.readyState === WebSocket.OPEN) {
          this.ws.close();
          // 
          await new Promise(resolve => {
            this.ws.onclose = () => resolve();
          });
        }
        this.ws = null;
        this.reconnectAttempts = 0;
        this.isReconnecting = false;
      }
    },

    initWebSocket() {
      try {
        const token = sessionStorage.getItem('token');
        const baseURL = this.$axios.defaults.baseURL.replace(/^http/, 'ws').replaceAll('/api', '');
        const wsURL = `${baseURL}/metrics/${token}`;

        this.ws = new WebSocket(wsURL);

        this.ws.onopen = () => {
          console.log('WebSocket connection established.');
          this.reconnectAttempts = 0;
        };

        this.ws.onmessage = (event) => {
          try {
            const {cpuUsage, memoryUsage, trafficData} = JSON.parse(event.data);
            this.updateCharts(cpuUsage, memoryUsage, trafficData);
          } catch (error) {
            console.error('Error parsing message:', error);
          }
        };

        this.ws.onclose = (event) => {
          console.log('WebSocket connection closed:', event);
        };

        this.ws.onerror = (error) => {
          console.error('WebSocket error:', error);
        };
      } catch (error) {
        console.error('Error initializing WebSocket:', error);
      }
    },

    updateCharts(cpuUsage, memoryUsage, trafficData) {
      // 
      if (this.cpuChart) {
        this.cpuChart.setOption({
          series: [{
            data: [
              {value: cpuUsage.used, name: ''},
              {value: cpuUsage.free, name: ''},
            ]
          }],
        });
      }

      if (this.memoryChart) {
        this.memoryChart.setOption({
          series: [{
            data: [
              {value: memoryUsage.used, name: ''},
              {value: memoryUsage.free, name: ''},
            ]
          }],
        });
      }

      if (this.trafficChart) {
        this.trafficChart.setOption({
          xAxis: {data: trafficData.timestamps},
          series: [
            {name: '', data: trafficData.inbound},
            {name: '', data: trafficData.outbound},
          ],
        });
      }
    }
  }
};

</script>

<style>
/*  */
@media screen and (max-width: 768px) {
  #map {
    height: 300px; /*  */
  }

  .card-container .el-col {
    width: 50%; /*  */
    margin-bottom: 10px;
  }

  .data-card {
    padding: 10px;
  }

  .data-card h3 {
    font-size: 14px;
  }

  .data-card p {
    font-size: 18px;
  }

  .chart {
    height: 300px; /*  */
  }

  .chart-container .el-col {
    width: 100%; /*  */
  }
}

#map {
  height: 600px;
  width: 100%;
  margin-top: 20px;
  margin-bottom: 20px;
  border-radius: 8px;
}

.card-container {
  margin-bottom: 30px;
}

.data-card {
//background: white; padding: 20px; border-radius: 8px; text-align: center; box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1); border: 1px solid #ebebeb;
}

.data-card h3 {
  margin: 0;
  font-size: 16px;
//color: #888;
}

.data-card p {
  margin: 0;
  font-size: 24px;
  font-weight: bold;
//color: #333;
}

.chart-container {
  display: flex;
  flex-wrap: wrap;
}

.chart {
  width: 100%;
  height: 500px;
  margin-bottom: 20px;
//background: #fff; border: 1px solid #ebebeb; border-radius: 8px; box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}
</style>
